@keyframes class1 {
  0% {
    transform: translateX(0px);
    z-index: 1;
  }
  33% {
    transform: translateX(-200px);
    z-index: 3;
  }
  66% {
    transform: translateX(200px) rotate(360deg);
    z-index: 3;
  }
  100% {
    transform: translateX(0px);
    z-index: 1;
  }
}
@keyframes class2 {
  0% {
    transform: translateX(0px);
    z-index: 3;
  }
  33% {
    transform: translateX(200px);
    z-index: 1;
  }
  66% {
    transform: translateX(-200px) rotateX(360deg);
    z-index: 1;
  }
  100% {
    transform: translateX(0px);
    z-index: 3;
  }
}
.class1 {
  animation: class1 2s ease-in-out infinite;
  height: 100px;
  width: 100px;
  border-radius: 50%;

  box-shadow: 0px 0px 10px rgba(0, 0, 0, rgba(0, 0, 0, 0.4));
}

.class2 {
  animation: class2 2s ease-in-out infinite;
  height: 100px;
  width: 100px;
  border-radius: 50%;

  box-shadow: 0px 0px 10px rgba(0, 0, 0, rgba(0, 0, 0, 0.4));
}

@keyframes class3 {
  0% {
    transform: rotate(0deg);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: rotate(360deg);
    opacity: 0;
  }
}
.class3 {
  animation: 1.2s ease-in-out infinite class3;
}
